// https://swas.io/blog/social-share-button-for-gatsby-blog-pages/
import React from "react"
import PropTypes from "prop-types"
import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  WhatsappShareButton,
  RedditShareButton,
  LinkedinIcon,
  EmailShareButton,
  EmailIcon,
  FacebookIcon,
  RedditIcon,
  TwitterIcon,
  WhatsappIcon,
  PocketShareButton,
  PocketIcon,
  InstapaperShareButton,
  InstapaperIcon,
  MailruShareButton,
  MailruIcon,
  OKIcon,
  OKShareButton,
  TumblrIcon,
  TumblrShareButton,
  ViberShareButton,
  ViberIcon,
  VKIcon,
  VKShareButton,
  WeiboShareButton,
  WeiboIcon, WorkplaceShareButton, WorkplaceIcon,
} from "react-share"

// import './Share.scss';

const Share = ({ socialConfig, tags }) => (
  <div className="post-social">
    <LinkedinShareButton url={socialConfig.config.url} className="button is-outlined is-rounded linkedin"
                         title={socialConfig.config.title}>
      <LinkedinIcon size={32} round={true}/>
    </LinkedinShareButton>
    <EmailShareButton url={socialConfig.config.url} subject={socialConfig.config.title} body={socialConfig.config.url}>
      <EmailIcon size={32} round={true}/>
    </EmailShareButton>
    <FacebookShareButton url={socialConfig.config.url} quote={socialConfig.config.title} hashtag={""}>
      <FacebookIcon size={32} round={true}/>
    </FacebookShareButton>
    <RedditShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <RedditIcon size={32} round={true}/>
    </RedditShareButton>
    <TwitterShareButton url={socialConfig.config.url} title={socialConfig.config.title} hashtags={tags}>
      <TwitterIcon size={32} round={true}/>
    </TwitterShareButton>
    <WhatsappShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <WhatsappIcon size={32} round={true}/>
    </WhatsappShareButton>
    <PocketShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <PocketIcon size={32} round={true}/>
    </PocketShareButton>
    <InstapaperShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <InstapaperIcon size={32} round={true}/>
    </InstapaperShareButton>
    <MailruShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <MailruIcon size={32} round={true}/>
    </MailruShareButton>
    <OKShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <OKIcon size={32} round={true}/>
    </OKShareButton>
    <TumblrShareButton url={socialConfig.config.url} title={socialConfig.config.title} tags={tags}>
      <TumblrIcon size={32} round={true}/>
    </TumblrShareButton>
    <ViberShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <ViberIcon size={32} round={true}/>
    </ViberShareButton>
    <VKShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <VKIcon size={32} round={true}/>
    </VKShareButton>
    <WeiboShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <WeiboIcon size={32} round={true}/>
    </WeiboShareButton>
    <WorkplaceShareButton url={socialConfig.config.url} title={socialConfig.config.title}>
      <WorkplaceIcon size={32} round={true}/>
    </WorkplaceShareButton>
  </div>
)

Share.propTypes = {
  socialConfig: PropTypes.shape({
    twitter: PropTypes.string.isRequired,
    config: PropTypes.shape({
      url: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
    }),
  }).isRequired,
  tags: PropTypes.arrayOf(PropTypes.string),
}
Share.defaultProps = {
  tags: [],
}

export default Share