import React from "react"
import { Link, graphql } from "gatsby"
import { MDXRenderer } from "gatsby-plugin-mdx"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { rhythm } from "../utils/typography"
import ArticleMeta from "../components/ArticleMeta"
import Share from "../components/Share"
import { DiscussionEmbed } from "disqus-react"
// import { MDXProvider } from "@mdx-js/react"
// import { LazyPlot } from "../components/Plotly"

// necessary for load with MDXProvider: https://mdxjs.com/advanced/components#caveats
// const components = {
//   LazyPlot,
// }

const BlogPostTemplate = ({ data, pageContext, location }) => {
  console.debug(data)

  const post = data.mdx
  const siteTitle = data.site.siteMetadata.title
  const twitter = data.site.siteMetadata.social.twitter
  const url = data.site.siteMetadata.siteUrl

  const disqusConfig = {
    // shortname: process.env.GATSBY_DISQUS_NAME,
    shortname: "sysid",
    config: { identifier: post.fields.slug, title: post.frontmatter.title },
  }

  // const readingTime = post.fields.readingTime.minutes
  const { previous, next } = pageContext

  const meta = {
    meta: post.frontmatter,
    slug: post.fields.slug,
    readingTime: post.fields.readingTime,
  }

  return (
    // <MDXProvider components={components}>
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <article>
        <header
          style={{
            marginBottom: rhythm(1),
          }}
        >
          <h1
            style={{
              marginTop: rhythm(1),
              marginBottom: 0,
            }}
          >
            {post.frontmatter.title}
          </h1>
          <Share
            socialConfig={{
              twitter,
              config: {
                url: `${url}${meta.slug}`,
                title: post.frontmatter.title,
              },
            }}
            tags={post.frontmatter.tags}
          />

          <ArticleMeta {...meta}/>
        </header>
        {/* injecting data via properties inot MDX */}
        {/*<MDXRenderer disqusConfig={disqusConfig}>{post.body}</MDXRenderer>*/}
        <MDXRenderer>{post.body}</MDXRenderer>
        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
        <footer>
          <Bio/>
        </footer>
      </article>

      <nav>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                ← {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>

      {/*this could be used individually in MDX file*/}
      <DiscussionEmbed {...disqusConfig} />

      <Share
        socialConfig={{
          twitter,
          config: {
            url: `${url}${meta.slug}`,
            title: post.frontmatter.title,
          },
        }}
        tags={post.frontmatter.tags}
      />

    </Layout>
    // </MDXProvider>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        siteUrl
        social {
          twitter
        }
      }
    }
    mdx(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 200)
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
      }
      fields {
        slug
        readingTime {
          minutes
        }
      }

    }
  }
`
